import React, { useState } from "react";
import {
  ArticleDetailsContainer,
  ArticleTextContainer,
  ArticleContainer,
  ArticlePic,
  ArticleTitle,
  ArticleDetail,
} from "../../components/general";
import {
  SemiTitle,
  TitleLabel,
  PersonalContainer,
  PersonalCircle,
  PersonalName,
  PersonalitiesContainer,
  SeeAll,
} from "./peopleStyle";
import Article from "../../components/article/Article";
import { warPrisoners, slainPeople } from "../people/consts";

export default function People() {
  const [personI, setPersonI] = useState();
  const [showCardO, setShowCardO] = useState(false);
  const [showCardT, setShowCardT] = useState(false);
  const [showWar, setShowWar] = useState(true);

  const containerStyle = {
    display: "flex",
    flexDirection: "row-reverse",
    textAlign: "center",
    color: "white",
    fontWeight: "bold",
    justifyContent: "flex-start",
    fontSize: "0.7rem",
    padding: "0.5rem",
    paddingTop: "1rem",
  };
  const selectedStyle = {
    // margin: "auto",
    marginRight: "0.25rem",
    color: "white",
    width: "fit-content",
    padding: "2px 12px",
    background: "#657c54",
    borderRadius: "20vw",
    fontSize: "15px",
  };
  const unselectedStyle = {
    // margin: "auto",
    marginRight: "0.25rem",
    width: "4.5rem",
    fontSize: "15px",
    textAlign: "center",
    padding: "2px 0px",
    background: "white",
    color: "black",
    borderRadius: "20vw",
    border: "2px solid #657c54",
  };

  return (
    <>
      <div style={containerStyle}>
        <SemiTitle
          onClick={() => {
            setShowWar((prevState) => !prevState);
          }}
          style={showWar ? selectedStyle : unselectedStyle}
        >
          {showWar ? "שבויי מלחמה" : "שבויים"}
        </SemiTitle>
        <SemiTitle
          style={!showWar ? selectedStyle : unselectedStyle}
          onClick={() => setShowWar((prevState) => !prevState)}
        >
          {!showWar ? "חללי מלחמה" : "חללים"}
        </SemiTitle>
      </div>
      {showWar &&
        warPrisoners.map((prisoner) => (
          <div style={{ display: "flex", justifyContent: "center" }}>
            <ArticleContainer
              onClick={() => {
                setShowCardO(true);
                setPersonI(prisoner);
              }}
            >
              <ArticlePic src={prisoner.pic} />
              <ArticleTextContainer>
                <ArticleTitle>{prisoner.name}</ArticleTitle>
                <ArticleTitle>{prisoner.years}</ArticleTitle>
                <ArticleDetailsContainer>
                  <ArticleDetail>{prisoner.unit}</ArticleDetail>
                </ArticleDetailsContainer>
              </ArticleTextContainer>
            </ArticleContainer>
          </div>
        ))}
      {showCardO && <Article setShowCard={setShowCardO} article={personI} />}
      {!showWar &&
        slainPeople.map((person) => (
          <div style={{ display: "flex", justifyContent: "center" }}>
            <ArticleContainer
              onClick={() => {
                setShowCardT(true);
                setPersonI(person);
              }}
            >
              <ArticlePic src={person.pic} />
              <ArticleTextContainer>
                <ArticleTitle>{person.name}</ArticleTitle>
                <ArticleTitle>{person.years}</ArticleTitle>
                <ArticleDetailsContainer>
                  <ArticleDetail>{person.unit}</ArticleDetail>
                </ArticleDetailsContainer>
              </ArticleTextContainer>
            </ArticleContainer>
          </div>
        ))}
      {showCardT && <Article setShowCard={setShowCardT} article={personI} />}
    </>
  );
}
